import { gql } from "@apollo/client";

export const STUDENT_REPORT = gql `
    query studentReport ($studentCode: String) {
        studentReport (studentCode: $studentCode) {
            school
            textTop
            textMid
            textBottom
            studentPhoto
            studentCode
        }
    }
`;

export const STUDENT_REPORT_SECTION = gql `
    query studentReportSection ($section: Int) {
        studentReportSection (section: $section) {
            school
            textTop
            textMid
            textBottom
            studentPhoto
            studentCode
        }
    }
`;

export const ALL_SECTIONS = gql `
    query allSections {
        allSections {
            id
            section
        }
    }
`;

export const STUDENT_MARK_REPORT = gql `
    query studentMarkReport ($studentCode: String) {
        studentMarkReport (studentCode: $studentCode) {
            school
            textTop
            textMid0
            textMid1
            textMid2
            textMid3
            textMid4
            textMid5
            textMid6
            textMid7
            textBottom
            studentPhoto
            studentCode
        }
    }
`;

export const STUDENT_MARK_REPORT_SECTION = gql `
    query studentMarkReportSection ($section: Int) {
        studentMarkReportSection (section: $section) {
            school
            textTop
            textMid0
            textMid1
            textMid2
            textMid3
            textMid4
            textMid5
            textMid6
            textMid7
            textBottom
            studentPhoto
            studentCode
        }
    }
`;

export const STUDENT_SCHOOL_YEAR = gql `
    query studentSchoolyear ($student: Int) {
        studentSchoolyear (student: $student) {
            id
            schoolyear
            season
            semesterCode
            startDate
            endDate
        }
    }
`;

export const STUDENT_MARK = gql`
    query studentMark ($student: Int, $schoolyear: Int, $part: String) {
        studentMark (student: $student, schoolyear: $schoolyear, part: $part) {
            id
            markBoard {
                subject {
                    subject 
                    subjectCode
                    credit
                }
            }
            markRelSet {
                markSetting {
                    name
                }
                markVal
            }
        }
    }
`

export const STUDENT_MARK_REL = gql `
    query studentMarkRel ($student: Int, $schoolyear: Int) {
        studentMarkRel (student: $student, schoolyear: $schoolyear) {
            id
            mark {
                student {
                    studentCode 
                }
                markBoard {
                    subject {
                        id
                        subject
                        subjectCode
                        credit
                    }
                    schoolyear {
                        schoolyear 
                        season
                    }
                }
            }
            markSetting {
                name
                percentage
            }
            markVal
        }
    }
`;

export const MARK_PERCENTAGE = gql `
    query markPercentage ($percentage: Int) {
        markPercentage (percentage: $percentage) {
            type
            percentage
            diam
        }
    }
`;

export const MARK_CON = gql `
    query markCon ($subject: Int!, $schoolyear: Int!, $student: Int!) {
        markCon (subject: $subject, schoolyear: $schoolyear, student: $student) {
            percentage
            markRel {
                markVal
                markSetting
            }
            diam
        }
    }
`;

export const MARK_CON_SUBJECT = gql `
    query markconSubject ($schoolyear: Int!, $section: Int!, $part: String!) {
        markconSubject (schoolyear: $schoolyear, section: $section, part: $part) {
            subjectId
            subject
            subjectCode
            subjectCredit
        }
    }
`;

export const MARK_CON_STUDENT = gql `
    query markconStudent ($section: Int!) {
        markconStudent (section: $section) {
            studentId
            familyName
            name
            studentCode
            registerNo
        }
    }
`;

export const MARK_CON_QUERY = gql`
    query markConQuery ($section: Int!, $schoolyear: Int!) {
        markconSubjectA: markconSubject (schoolyear: $schoolyear, section: $section, part: "A") {
            subjectId 
            subject
            subjectCode
            subjectCredit
        }
        markconSubjectB: markconSubject (schoolyear: $schoolyear, section: $section, part: "B") {
            subjectId 
            subject
            subjectCode
            subjectCredit
        }
        markconSubjectC: markconSubject (schoolyear: $schoolyear, section: $section, part: "C") {
            subjectId 
            subject
            subjectCode
            subjectCredit
        }
        markconStudent (section: $section) {
            studentId
            familyName
            name
            studentCode
            registerNo
        }
    },
`;

export const ALL_STUDENTS_REPORT = gql`
    query allStudentsReport {
        allStudentsReport {
            id
            user {
                id
                username
                email
            }
            studentCode
            surname
            familyName
            familyNameMgl
            name
            nameMgl
            religion
            registerNo
            nationality
            state
            photo
            phone
            phone2
            address
            citizen
            degree {
                id
                name
            }
            activity {
                id
                name
            }
            joinDate
            joinBefore
            sex
            birthdate
            classtime {
                id
                name
            }
            birthCity {
                id
                name
            }
            birthDistrict {
                id
                name
            }
            status {
                id
                name
            }
            statusExtra {
                id
                name
            }
            school {
                id
                name
            }
            classes {
                id
                classes
            }
            section {
                id
                section
            }
            program {
                id
                program
            }
            joinSchoolyear {
                id
                schoolyear
            }
        }
    }
`